import React, { useState, useEffect } from 'react';
import '../Components.css';

const words = ['a portfolio', 'a blog', 'an e-commerce shop', 'a landing page', 'an onboarding page']; // Your array of words

function ServiceDetails() {
  const [currentWord, setCurrentWord] = useState('');

  useEffect(() => {
    let index = 0;
    const wordInterval = setInterval(() => {
      setCurrentWord(words[index]);
      index = (index + 1) % words.length;
    }, 2000); // Change words every 2 seconds

    return () => {
      clearInterval(wordInterval); // Clean up on component unmount
    };
  }, []); // Empty dependency array because words is now constant

  return (
    <section id="serviceDetails">
      <div className="serviceDetails__wrapper">
        <div className="serviceDetails__content">
          <h2>Need <span className="word">{currentWord}</span>?</h2>
          <p>
            We're smashing the barriers of entry into the digital world with our unbeatable offer. We'll create a professional, responsive, one-page website tailored to your needs.
            No hidden fees, no monthly charges. <br/><br/>Even hosting setup is on us! All you need to cover is your domain name.
            Our mission? To provide an affordable digital solution for small businesses, start-ups, and individuals. <br/><br/>With our expert team handling your online presence, you can focus on what matters - growing your business.
            Lets turn your digital dreams into reality!
          </p>
        </div>
        <img src="https://s3.amazonaws.com/kwatt.engineering/price_tag_promise.png" alt="Service Details" className="serviceDetails__image" />
      </div>
    </section>
  );
}

export default ServiceDetails;
