import React, { useEffect, useState } from 'react';
import Scrollspy from 'react-scrollspy';
import { Link } from 'react-scroll';
import '../Components.css';

function Header() {
  const [isSticky, setIsSticky] = useState(false);
  const [menuOpen, setMenuOpen] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      const scrollPosition = window.pageYOffset;
      const header = document.querySelector('header');

      if (scrollPosition > 0) {
        setIsSticky(true);
        header.classList.add('sticky');
      } else {
        setIsSticky(false);
        header.classList.remove('sticky');
      }
    };

    window.addEventListener('scroll', handleScroll);
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);

  const handleMenuClick = () => {
    setMenuOpen(!menuOpen);
  };

  return (
    <header className={isSticky ? 'sticky' : ''}>
      <img src="https://s3.amazonaws.com/kwatt.engineering/Kilowatt_kweb_services.png" alt="Logo" className="logo" />
      <button onClick={handleMenuClick} className="menu-button">
      <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor" className="hamburger-icon">
        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16" />
      </svg>

      </button>
      <Scrollspy items={['serviceDetails', 'hostingPackages', 'faq', 'contactUs']} currentClassName="is-current" className={menuOpen ? 'nav-links open' : 'nav-links'}>
        <li>
          <Link to="serviceDetails" smooth={true} duration={500} onClick={handleMenuClick}>Our offer</Link>
        </li>
        <li>
          <Link to="hostingPackages" smooth={true} duration={500} onClick={handleMenuClick}>Website Packages</Link>
        </li>
        <li>
          <Link to="faq" smooth={true} duration={500} onClick={handleMenuClick}>FAQs</Link>
        </li>
        <li>
          <Link to="contactUs" smooth={true} duration={500} onClick={handleMenuClick}>Contact Us</Link>
        </li>
      </Scrollspy>
    </header>
  );
}

export default Header;
